export default {
	data() {
		return {
			gid:null,
			goods:[],
			detail:'',
			scrollTop:0,
			iconStyle: {
				fontSize: '32rpx',
				color: '#2979ff'
			}
		};
	},
	onLoad(e) {
		this.getGoods(e.gid);
	},
	onPageScroll(e) {
			this.scrollTop = e.scrollTop;
	},
	computed:{
		cartCount(){
			return uni.getStorageSync('cart') ? uni.getStorageSync('cart').length : 0 
		}
	},
	methods:{
		//获取平台商城商品信息
		async getGoods(gid){
			var res = await this.$request.get('/pshop', );
			var arr = [];
			this.goods = res.data.result;
			for(var i in this.goods){
				this.goods[i].goods.find((item)=>{
					arr.push(item)
				})
			}
			this.detail = arr.find((one)=>{
				return one['gid'] == gid;
			})
			console.log(this.detail)
		},
		// cartCount(){
		// 	uni.getStorageSync('cart')
		// },
		
		goCart(){
			uni.navigateTo({
				url:'/pages/user/cart'
			})
		},
	}
}
